<template>
  <div class="author">
    <div style="display: flex; align-items: center;">
      <label>Story Name </label>
      &emsp;
      <input v-model="inputTitle">
      <span style="flex-grow: 1;"></span>
      <button class="button" @click="importFile()">Import Story </button>
    </div>
    <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
    <div>
      <!-- Vue的模板语法允许我们使用v-on:click指令来监听段落的点击事件，然后通过调用handleWordClick方法来检测点击的单词。-->
      <p class="preserve-line-breaks" @click="handleWordClick">{{ para }}</p>
    </div>
    <div>
      <div>
        <textarea v-model="mindText" v-if="developer"></textarea>
        <button v-on:click="generateMindMap" v-if="developer">generate MindMap</button>
        <div v-html="mindMap" @click="handleWordClick"></div>
      </div>
      <textarea class="responsive-textarea" v-model="inputText" placeholder="write story here"></textarea>
      <br /><br />
      <label>From para index (behind sentence): </label>
      <input v-model="paraIndexFrom" />
      <br /><br />
      <label>From the para word (click) "{{ wordNode }}".</label>
      <input v-model="wordNode" />
      <br /><br />
      <label>What para index go to: </label>
      <input v-model="paraIndexGoTo" />

      <br /><br />
      <!-- @=v-on -->
      <button class="button" @click="submitPara">Submit</button>
      &emsp;&emsp;&emsp;&emsp;&emsp;&emsp;
      <button class="button" @click="downloadPara">Download Story</button>
    </div>
  </div>
</template>

<script >
import { saveAs } from 'file-saver'
import mermaid from 'mermaid';
export default {
  data() {
    return {
      developer: false,
      step: 1,
      inputTitle: 'None',
      wordNode: ' ',
      paraIndexFrom: 0,
      paraIndexGoTo: 1,
      goBack: false,
      para: ' ',
      inputText: '',
      allStory: '',
      index: 0,
      paraList: ["Story Begin"],
      paraMindList: ["Story Begin [0]"],
      text: '',
      graph: '',
      mindText: '0["Story Begin"]',
      mindMap: '',
      alpha: '',
      novel: {
        title: '',
        author: '',
        content: ''
      },
    }
  },
  methods: {
    importFile() {
      document.getElementById('file-input').click();
    },
    onFileInputChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = (event) => {
        const text = event.target.result;
        this.parseNovelText(text);
      };
      reader.readAsText(file);
    },
    parseNovelText(text) {
      const lines = text.trim().split('\n');
      let title = '';
      let author = '';
      let content = '';
      for (let line of lines) {
        if (line.startsWith('Story Title: ')) {
          title = line.slice(13);
        } else if (line.startsWith('Authors: ')) {
          author = line.slice(9);
        } else {
          content += line + '\n';
        }
      }
      this.novel.title = title;
      this.inputTitle = title;
      this.novel.author = author;
      this.novel.content = content;
      const nodes = content.split('\n');

      nodes.forEach(paragraphText => {
        const matches = paragraphText.match(/\[(\d+)\] \[(.*?)\] (.+) \[(\d+)\]/);
        if (matches) {
          const sourceIndex = parseInt(matches[1]);
          const wordIndex = matches[2];
          const content = matches[3];
          const paragraphIndex = parseInt(matches[4]);

          this.paraIndexFrom = sourceIndex;
          this.wordNode = wordIndex;
          this.inputText = content;
          this.paraIndexGoTo = paragraphIndex;
          this.submitPara(false);
        }
      });
      this.generateMindMap();
    },

    submitPara(updateMindMap = true) {
      if (this.paraIndexFrom > this.index || this.paraIndexGoTo > this.index + 1) {
        alert("符合下标的段落不存在或跳段了,请检查来自下标或者Go to下标");
      } else {
        //前往之前的节点不应该使index增长，节约资源
        //In fact, only this.paraIndexGoTo = this.index + 1 is true
        if (this.paraIndexGoTo > this.index) {
          this.index += 1;
        }
        //是否来自之前的节点，而不是上一个段落
        if (this.paraIndexFrom < this.index) {
          this.mindText += '\n' + this.paraIndexFrom + '["' + this.paraMindList[this.paraIndexFrom] + '"]';
        }
        //mermaid数字标签具有唯一性
        //是否带有条件
        this.mindText += this.wordNode === ' ' ? '--->' : '-->|' + this.wordNode + '|';
        //是否前往之前的节点，而不是下一个段落
        this.goBack = this.paraIndexGoTo === this.index ? false : true

        this.paraList.push(this.inputText);
        this.paraMindList.push(this.inputText + ' [' + this.index + ']');
        this.alpha = this.inputText
        this.inputText = '[' + this.paraIndexFrom + '] ' + '[' + this.wordNode + '] ' + this.inputText;
        if (this.goBack) {
          this.mindText += this.paraIndexGoTo + '["' + this.paraMindList[this.paraIndexGoTo] + '"]';
          this.para += this.inputText + ' [' + this.paraIndexGoTo + ']' + '\n';
          this.allStory += this.inputText + ' [' + this.index + ']' + '\n';
        } else {
          this.mindText += this.index + '["' + this.alpha + '[' + this.index + ']"]';
          this.para += this.inputText + ' [' + this.paraIndexGoTo + ']' + '\n';
          this.allStory += this.inputText + ' [' + this.index + ']' + '\n';
        }

        this.inputText = '';
        this.wordNode = ' ';
        this.paraIndexFrom = this.paraIndexGoTo;
        this.paraIndexGoTo = this.index + 1;
        if (updateMindMap) {
          this.generateMindMap();
        }

      }

    },
    downloadPara() {
      var blob = new Blob(["Story Title: " + this.inputTitle + '\n' + this.allStory], {
        type: "text/plain;charset=utf-8"
      });
      saveAs(blob, "story.txt");
    },

    handleWordClick() {
      const selection = window.getSelection();
      const range = selection.getRangeAt(0);
      const text = range.toString().trim();

      if (text !== '') {
        // Find the paragraph index containing the selected text
        const targetTextNode = range.endContainer;
        const textAfterSelection = targetTextNode.textContent.slice(range.endOffset);
        const nextParaIndexMatch = textAfterSelection.match(/\[(\d+)\]/);

        if (nextParaIndexMatch) {
          const paraIndex = parseInt(nextParaIndexMatch[1], 10);
          this.paraIndexFrom = paraIndex;
        }

        this.wordNode = text;
      }
    },
    async generateMindMap() {
      // 将输入文本转换为mermaid语言格式
      //const mermaidText = `graph TB\n${this.mindText.split('\n').map(text => `  ${text}`).join('\n')}`;
      const mermaidText = `graph TB\n${this.mindText}`;
      // 生成MindMap
      mermaid.initialize();
      const result = await mermaid.mermaidAPI.render('mindMapSvg', mermaidText);
      const svg = result.svg;
      this.mindMap = svg;
    }
  },
};
</script>

<style>
.author {
  width: 70%;
  margin-left: 15%;
  margin-right: 15%;
}

.preserve-line-breaks {
  white-space: pre-wrap;
}

.responsive-textarea {
  width: 100%;
  height: 20vh;
}

.button {
  background-color: #10b1e8;
  border: none;
  color: white;
  padding: 1em 1em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1em;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 1em;
  box-shadow: 0 4px #999;
  transition-duration: 0.4s;
}

.button:hover {
  box-shadow: 0 8px #999;
  transform: translateY(-2px);
}
</style>